export function timeToRead (words) {
  if (!words || typeof words !== "number")  return null

  const wordsByMinutes = 230;
  return Math.round(words / wordsByMinutes)
}

export function timeToReadMessage (words) {
  const time = timeToRead(words)
  if (typeof time !== "number") return null
  return `< ${time + 1} ${time < 1 ? "minute" : "minutes"}`
}